<template>
  <el-form
    :model="form"
    status-icon
    :rules="rules"
    ref="form"
    label-width="100px"
    class="login-container"
    ><!--status-icon用于表单校验图标 rules校验-->
    <h3 class="login-title">系统登录</h3>
    <el-form-item
      label="用户名"
      label-width="80px"
      prop="userName"
      class="userName"
    >
      <el-input
        type="input"
        v-model="form.userName"
        autocomplete="off"
        placeholder="请输入账号"
      ></el-input>
    </el-form-item>
    <el-form-item label="密码" label-width="80px" prop="password">
      <el-input
        type="password"
        v-model="form.password"
        autocomplete="off"
        placeholder="请输入密码"
      ></el-input>
    </el-form-item>
    <el-form-item class="login-submit-father">
      <el-button type="primary" @click="login" class="login-submit-son"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import { getMenu } from "../../api/data";
export default {
  name: "Login",
  data() {
    return {
      form: {},
      rules: {
        //校验用户名和密码
        userName: [
          {
            required: true, //必填
            message: "请输入用户名",
            trigger: "blur",
          },
          {
            min: 3,
            max: 16,
            message: "用户名长度不能小于3位且不能大于16位",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    login() {
      getMenu(this.form).then(({ data: response }) => {
        if (response.code === 20000) {
          this.$store.commit("clearMenu");
          this.$store.commit("setMenu", response.data.menu);
          this.$store.commit("setToken", response.data.token);
          this.$store.commit("addMenu", this.$router);
          this.$router.push({ name: "home" });
        } else {
          this.$message.warning(response.data.message);
        }
      });
    },
  },
};
</script>

<style scoped>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login-submit {
  margin: 10px auto 0 auto;
}
</style>
